export default {
	data() {
		return {
			comPonyList:[],
			ListLen:0,
			SearchInput:"",
			branch:[],
			array: [],
			array1:[0,1],
			index: "",
			pull:0,
		}
	},
	onLoad:function(){
		var _self = this
		// wx.showShareMenu() 
		// if(!uni.getStorageSync("openId")){
		// 	uni.navigateTo({
		// 		url:"../load/load"
		// 	})
		// }
		_self.getInfo()
		_self.getBranchQuery()
	},  
	onPullDownRefresh:function() {
		var _self = this
		// if(!uni.getStorageSync("openId")){
		// 	uni.navigateTo({
		// 		url:"../load/load"
		// 	})
		// }
		_self.getInfo()
		_self.getBranchQuery()
	},
	methods: {
		bindPickerChange: function(e) {
			var _self = this
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value 
			this.pull = _self.array1[_self.index]
			_self.getInfo()
		},
		//http://java.gzbigbang.cn/ybyfManager/dockingManager/cardLower
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/stationedQuery",
				data:{id:"0",pull:_self.pull,name:_self.SearchInput},
				method:"GET",
				success:function(res){
					console.log(res)
					_self.comPonyList = res.data
					_self.ListLen = _self.comPonyList.length
					console.log(res.data.length)
				}
			}) 
		}, 
		/**
		 * 获取分类
		 */ 
		getBranchQuery:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/branchQuery",
				data:{},
				method:"GET",
				success:function(res){
					console.log(res)
					var array=[]
					var array1=[]
					res.data.push({name:"全部",id:0})
					for(var i = 0;i<res.data.length;i++){
						array[i] =  res.data[i].name
						array1[i] = res.data[i].id
					}
					_self.array = array
					_self.array1 = array1
					 uni.stopPullDownRefresh();
				}
			}) 
			
		},
		searchInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/stationedQuery",
				data:{id:0,name:_self.SearchInput,pull:_self.pull},
				method:"GET",
				success:function(res){
					console.log(res)
					// _self.comPonyList = res.data.slice(0,10)
					_self.comPonyList = res.data
					_self.ListLen = _self.comPonyList.length
				}
			}) 
		},
		gotoDetail:function(id){
			uni.navigateTo({
				url:"../componyEnterDetail/componyEnterDetail?id="+id
			})
		},
	},
	filters:{
		changeData:function(data){
			 if(data == undefined || data==null || data ==''){
				 return '暂无'
			 }else{
				 return data
			 }
		}
	},
	watch:{
		SearchInput:function(newData){
			var _self = this
			if(newData == ""){
				_self.getInfo()
			}else{
				_self.searchInfo()
			}
		}
	},
	
}
